import nexo, { connect, iAmReady, getStoreInfo as nexoGetStoreInfo } from '@tiendanube/nexo';

let nexoClient = null;
let isConnected = false;

export const initNexo = async () => {
  try {
    nexoClient = nexo.create({
      clientId: '21916',
      log: true
    });

    await connect(nexoClient);
    isConnected = true;

    // Notificar que o app est pronto
    iAmReady(nexoClient);

    console.log('Nexo conectado com sucesso');
    return nexoClient;
  } catch (error) {
    console.error('Erro ao conectar com Nexo:', error);
    throw error;
  }
};

export const getNexoClient = () => {
  if (!nexoClient) {
    console.warn('Nexo client no foi inicializado');
  }
  return nexoClient;
};

export const isNexoConnected = () => isConnected;

// Funes auxiliares para navegao
export const navigateToApp = (route) => {
  if (nexoClient) {
    nexoClient.navigate(route);
  }
};

export const goBack = () => {
  if (nexoClient) {
    nexoClient.goBack();
  }
};

// Funo para mostrar toast/notificaes
export const showToast = (message, type = 'success') => {
  if (nexoClient) {
    nexoClient.showToast({
      message,
      type, // 'success', 'error', 'warning', 'info'
      duration: 3000
    });
  }
};

// Funo para obter informaes da loja
export const getStoreInfo = async () => {
  if (!nexoClient) {
    throw new Error('Nexo client no est inicializado');
  }

  try {
    const storeInfo = await nexoGetStoreInfo(nexoClient);
    return storeInfo;
  } catch (error) {
    console.error('Erro ao obter informaes da loja:', error);
    throw error;
  }
};

// Funo para obter o storeId do ACTION_AUTH_SESSION_TOKEN
export const getStoreId = async () => {
  if (!nexoClient) {
    throw new Error('Nexo client no est inicializado');
  }

  try {
    const sessionToken = await nexoClient.getAction('ACTION_AUTH_SESSION_TOKEN');
    return sessionToken?.storeId || null;
  } catch (error) {
    console.error('Erro ao obter storeId:', error);
    throw error;
  }
};
